import React, { useEffect } from 'react';
import './CommunityImpactSection.css';

function CommunityImpactSection() {

  useEffect(() => {
    const section = document.querySelector('.community-impact-section');

    const handleScroll = () => {
      const sectionTop = section.getBoundingClientRect().top;
      const windowHeight = window.innerHeight;

      if (sectionTop < windowHeight - 100) {
        section.classList.add('visible');
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <section className="community-impact-section">
      <h2>Impacto Comunitrio</h2>

      <div className="impact-content">
        <div className="text-content">
          <p>Nosso projeto vai alm de transformar terrenos baldios: ele visa criar verdadeiros osis urbanos, onde antes havia abandono. Esses espaos revitalizados se tornam reas de convivncia e lazer, promovendo o crescimento de reas verdes que impactam diretamente a qualidade de vida e a sade mental dos moradores. Com rvores e vegetao nativa, os locais antes negligenciados ganham vida, oferecendo sombra, ar fresco e um ambiente mais saudvel para todos.</p>
        </div>
      </div>

      <div className="impact-content reverse">
        <div className="text-content">
          <p>Alm de contribuir para a beleza esttica da vizinhana, esses espaos ajudam a combater as ilhas de calor que tanto afetam as cidades, diminuindo a poluio do ar e tornando o clima local mais ameno. Cada rea revitalizada se torna um ponto de encontro seguro e inspirador, onde a comunidade pode realizar atividades ao ar livre, cultivar relacionamentos e participar de prticas de educao ambiental, promovendo conhecimento e respeito pela natureza.</p>
        </div>
      </div>

      <div className="impact-content">
        <div className="text-content">
          <p>Nosso projeto conecta cidados em torno de um propsito comum: construir uma cidade mais sustentvel e integrada. Por meio da participao ativa e do compromisso ambiental, promovemos uma cultura de responsabilidade compartilhada, onde cada ao faz diferena. Cada morador que contribui com o projeto est, de fato, construindo um legado.</p>
        </div>
      </div>

      <div className="impact-content">
        <div className="text-content-phrase">
          <p>Um presente para a atual gerao e um futuro melhor para as prximas, enraizando valores de sustentabilidade e unio que perduraro no tempo.</p>
        </div>
      </div>
    </section>
  );
}

export default CommunityImpactSection;
