import React, { useEffect } from 'react';
import './TechInnovationSection.css';
import sustainableImage1 from '../assets/sustainable1.png'; // Imagem de inovao sustentvel 1
import sustainableImage2 from '../assets/sustainable2.png'; // Imagem de inovao sustentvel 2                       
import sustainableImage3 from '../assets/sustainable3.png'; // Imagem de inovao sustentvel 2                       

function TechInnovationSection() {

  useEffect(() => {
    const section = document.querySelector('.tech-innovation-section');

    const handleScroll = () => {
      const sectionTop = section.getBoundingClientRect().top;
      const windowHeight = window.innerHeight;

      if (sectionTop < windowHeight - 100) {
        section.classList.add('visible');
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <section className="tech-innovation-section">
      <h2>Inovao Tecnolgica</h2>

      <div className="tech-content">
        <div className="text-content">
          <p>Combinando tecnologias como reconhecimento de imagem e geolocalizao, nosso projeto utiliza modelos avanados para identificar e mapear terrenos baldios em reas urbanas, promovendo iniciativas de arborizao e sustentabilidade.</p>
        </div>
        <div className="image-content">
          <img src={sustainableImage1} alt="Inovao Sustentvel 1" className="tech-image" />
        </div>
      </div>

      <div className="tech-content">
        <div className="image-content">
          <img src={sustainableImage2} alt="Inovao Sustentvel 2" className="tech-image" />
        </div>
        <div className="text-content">
          <p>Utilizamos a API do Google Maps para exibir, em um mapa interativo, os terrenos identificados, permitindo que empresas e prefeituras planejem aes de plantio de rvores com maior eficincia e preciso.</p>
        </div>
      </div>
      
      <div className="tech-content">
        <div className="text-content">
          <p>Para incentivar proprietrios de terrenos a participarem, oferecemos um sistema de benefcios fiscais, como descontos no IPTU, ao cederem suas reas para projetos de arborizao, ajudando a expandir a cobertura verde das cidades.</p>
        </div>
        <div className="image-content">
          <img src={sustainableImage3} alt="Benefcio Sustentvel" className="tech-image" />
        </div>
      </div>
    </section>
  );
}

export default TechInnovationSection;
