import React, { useEffect, useRef } from 'react';
import './SustainabilitySection.css';
import biodiversidadeIcon from '../assets/biodiversidade.png';
import transparenciaIcon from '../assets/transparencia.png';
import comunitarioIcon from '../assets/comunitario.png';

function SustainabilitySection() {
  const sectionRef = useRef(null);

  useEffect(() => {
    const section = sectionRef.current;
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            section.classList.add('animate');
            observer.unobserve(section); // Remove o observador aps a animao comear
          }
        });
      },
      { threshold: 0.5 } // Inicia a animao quando 50% da seo estiver visvel
    );

    if (section) {
      observer.observe(section);
    }

    return () => {
      if (section) observer.unobserve(section);
    };
  }, []);

  return (
    <section className="sustainability-section" ref={sectionRef}>
      <div className="sustainability-item">
        <img src={biodiversidadeIcon} alt="Biodiversidade" className="sustainability-icon" />
        <h2>Biodiversidade</h2>
        <p>
          Transformando terrenos baldios em reas verdes, promovemos a biodiversidade e o equilbrio ambiental nas cidades.
        </p>
      </div>
      <div className="sustainability-item">
        <img src={transparenciaIcon} alt="Transparncia" className="sustainability-icon" />
        <h2>Transparncia</h2>
        <p>
          Nosso sistema oferece uma plataforma transparente para rastrear o progresso da recuperao ambiental dos terrenos.
        </p>
      </div>
      <div className="sustainability-item">
        <img src={comunitarioIcon} alt="Engajamento Comunitrio" className="sustainability-icon" />
        <h2>Engajamento Comunitrio</h2>
        <p>
          Trabalhamos com as comunidades para garantir o sucesso e a sustentabilidade a longo prazo dos projetos de revitalizao.
        </p>
      </div>
    </section>
  );
}

export default SustainabilitySection;
